  caite: ['intr. y prnl. Desplazarse un cuerpo de arriba abajo por la accin de su propio peso: cay desde el octavo piso.', 'Perder un cuerpo el equilibrio: se cay al subir las escaleras.'],
  cajn: ['m. Caja grande,  normalmente de madera: compr un cajn de fruta.', 'Compartimento de un mueble que se puede sacar y meter en ciertos huecos hechos para este fin: los cajones del armario.'],
  caln: ['tr. Penetrar un lquido en un cuerpo permeable. Tambin intr.: el agua ha calado hasta el stano.', 'Atravesar un cuerpo con un objeto punzante: la broca cal hasta la otra pared.'],
  clao: ['m. Nombre comn de varias aves coraciformes trepadoras de gran tamao,  plumas oscuras y fuerte pico curvo con un voluminoso apndice crneo: el clao habita las islas del ocano Pacfico.'],
  calar: ['tr. Penetrar un lquido en un cuerpo permeable. Tambin intr.: el agua ha calado hasta el stano.', 'Atravesar un cuerpo con un objeto punzante: la broca cal hasta la otra pared.'],
  calca: ['tr. Obtener una copia de un trazo o escritura por medio de un papel transparente o de calco: calc los dibujos del bordado.', 'Imitar o reproducir con exactitud: ha calcado los gestos de su ta.  Se conj. como sacar.'],
  calce: ['m. Llanta de una rueda.', 'Calza que se pone para suplir la falta de altura: se puso un calce en el zapato.'],
