  cals: ['adj. y com. gitano:  raza cal.  No confundir con cal.'],
  cliz: ['m. Vaso sagrado donde se consagra el vino en la misa: el sacerdote levant el cliz durante la consagracin.', 'bot. Cubierta externa de las flores completas,  formada por hojas verdes o spalos.'],
  calle: ['f. Va pblica en una poblacin: calle de Alcal.', 'Todo lo que en una poblacin est fuera de las viviendas: el mdico le ha dicho que todava no puede salir a la calle.'],
  callo: ['m. Dureza que por roce o presin se forma en la piel,  especialmente en los pies y en las manos: me ha salido un callo en el dedo de tanto apretar el bolgrafo.', 'col. Persona muy fea o desagradable de trato: este tipo es un callo.'],
  calma: ['f. Estado de la atmsfera cuando no hay viento: tras la tempestad viene la calma.', 'Paz,  tranquilidad: lo ms importante es no perder la calma.'],
  calmo: ['adj. Descansado,  en reposo: da calmo.'],
  calor: ['m. Energa producida por la vibracin acelerada de las molculas,  que se manifiesta elevando la temperatura y dilatando los cuerpos y llega a fundir los slidos y a evaporar los lquidos. A veces f.: en verano,  cuando hace la calor.', 'Temperatura corporal o ambiental elevada,  superior a la normal: en julio hace mucho calor.'],
  calva: ['f. Vase calvo,  va.'],
  calvo: ['adj. Que ha perdido el cabello. Tambin s.: de joven tena mucho pelo,  pero ahora es completamente calvo.', 'Pelado,  sin vegetacin: un jardn calvo y descuidado.'],
  calza: ['f. calzo,   cua con que se calza.', 'Media.'],
  calzo: ['m. Cua que se introduce entre dos cuerpos para calzar uno de ellos: pon un calzo bajo la alacena que cojea.', 'pl. Extremidades de un caballo o yegua,  sobre todo cuando son de un color distinto al del resto del cuerpo.'],
  campa: ['f. Tierra que carece de arbolado y por lo comn solo sirve para la siembra de cereales.'],
  campo: ['m. Terreno extenso sin edificar fuera de las poblaciones: su casa est en las afueras,  y hacia el horizonte se extiende el campo.', 'Tierra cultivable: campo de cebada.'],
  canal: ['m. Estrecho martimo,  natural o artificial: canal de Suez.', 'Cada una de las bandas de frecuencia en que puede emitir una estacin de televisin o de radio: solo recoge tres canales.'],
  candi: ['adj. Vase azcar candi o cande.'],
  cando: ['tr. Cerrar con llave o candado: no te olvides de candar la puerta al salir.', 'P. ext.,  cerrar de cualquier modo: candar una caja fuerte.'],
  cania: ['adj. col. Tpico,  folclrico: Espaa ca.', 'adj. y com. [Gitano] de raza: tez ca.  pl. cas.'],
  canje: ['m. Cambio,  trueque o sustitucin: te propongo un canje,  tu coleccin de vdeos por la ma de cmics antiguos.'],
  canoa: ['f. Embarcacin de remo o con motor,  estrecha,  sin quilla y generalmente de una pieza. La proa y la popa tienen la misma forma y suelen terminar en punta: los indgenas cruzaban el ro en canoa.'],
  cnon: ['m. Pieza hueca y larga,  a modo de caa: los caones del rgano.', 'Tubo por donde sale el proyectil de un arma de fuego: el can humeaba tras el disparo.'],
  canso: ['tr. y prnl. Causar cansancio,  fatigar: subir las escaleras ya me cansa menos.', 'Aburrir,  hartar: comer todos los das pollo acaba cansando.'],
  canta: ['m. Composicin potica con msica a propsito para ser cantada,  o adaptable a los aires populares: el Nuevo Mester de Juglara recupera viejos cantares.', 'cantar de gesta Poesa popular de la poca medieval en que se referan,  en principio oralmente,  hechos de personajes histricos,  legendarios o tradicionales: el "Poema de Mio Cid" es un cantar de gesta.'],
  cante: ['m. Cualquier tipo de canto popular,  especialmente el andaluz: es un entendido en cante y baile flamenco.', 'col. Olor fuerte y molesto: vaya cante a sudor!'],
  canto: ['m. Emisin de sonidos melodiosos con la voz,  sea la de un hombre o la de un animal: el canto del ruiseor.', 'Sonido estridente que producen algunos insectos haciendo vibrar ciertas partes de su cuerpo,  sobre todo las alas o litros: el canto de la cigarra.'],
  caal: ['m. Estrecho martimo,  natural o artificial: canal de Suez.', 'Cada una de las bandas de frecuencia en que puede emitir una estacin de televisin o de radio: solo recoge tres canales.'],
  caeo: ['tr. col. Dar una paliza: si no me marcho ahora mismo me van a canear en mi casa.'],
  can: ['m. Pieza hueca y larga,  a modo de caa: los caones del rgano.', 'Tubo por donde sale el proyectil de un arma de fuego: el can humeaba tras el disparo.'],
  caoba: ['f. rbol americano de la famila de las meliceas,  de hasta de 30 m de altura,  con tronco recto y grueso,  hojas compuestas,  flores pequeas y blancas y fruto capsular y leoso. Su madera es muy estimada para fabricar muebles,  por su color rojizo y por poderse pulimentar fcilmente.', 'Madera de este rbol.'],
  capar: ['tr. Extirpar o inutilizar los rganos genitales,  castrar: antiguamente se capaba a los hombres que iban a ejercer de eunucos en un harn.'],
  capaz: ['adj. Que tiene capacidad para contener algo.', 'Grande o espacioso: un auditorio capaz para dos mil personas.'],
  capea: ['f. Fiesta en la que se lidian becerros o novillos por aficionados: los novillos de la capea fueron muy mansos.'],
  capeo: ['tr. Hacer suertes con la capa al toro.', 'Entretener a uno con evasivas: cape a la polica mientras sus compinches escapaban.'],
  capia: ['f. amer. Maz blanco y muy dulce que se emplea en la preparacin de postres y golosinas.', 'amer. Dulce o masa que se hace con harina de capia y azcar.'],
  capn: ['adj. y m. [Hombre o animal] que ha sido castrado.', 'm. Pollo que se castra y se ceba para comerlo.'],
