  casia: ['adv. c. Poco menos de,  cerca de,  con corta diferencia,  por poco: tengo casi treinta aos.', 'adv. m. Indica indecisin: casi me voy a quedar un rato.'],
  casis: ['adv. c. Poco menos de,  cerca de,  con corta diferencia,  por poco: tengo casi treinta aos.', 'adv. m. Indica indecisin: casi me voy a quedar un rato.'],
