  cazar: ['tr. Buscar y perseguir a ciertos animales para apresarlos o para matarlos: en Inglaterra es popular salir a cazar zorros.', 'col. Atrapar,  conseguir algo difcil con maa: hemos cazado un buen aumento de sueldo.'],
  cazn: ['m. Pez condrictio marino de hasta 2 m,  muy voraz,  de dientes agudos y cortantes,  de carne comestible y cuya piel seca sirve de lija: el cazn en adobo es una tapa muy tpica en Andaluca.'],
  cebar: ['tr. Engordar a un animal,  especialmente cuando se desea aprovechar su carne para el consumo: estuvieron cebando al pavo de la cena de Navidad durante casi dos meses.', 'P. ext.,  alimentar a una persona abundantemente: tu madre te est cebando. Tambin prnl.'],
