  cejar: ['intr. Aflojar o ceder en un empeo o una determinacin.  Se usa ms en frases negativas y seguido de la prep. en:  no cejaremos en nuestro empeo de lograr la paz.'],
  celda: ['f. Cada una de las habitaciones donde se recluye a los presos de una crcel.', 'Aposento o cuarto individual de un convento o de un internado: los cartujos tienen celdas muy pequeas y austeras.'],
  cella: ['f. arquit. Espacio interior que constituye el ncleo de la construccin en los templos griegos y romanos. De forma rectangular,  comunica por uno de sus lados con el pronaos o prtico.'],
