  celta: ['adj. y com. De los antiguos pueblos indoeuropeos cuyo apogeo se localiz en el centro y oeste de Europa entre los siglos vi y i a. C.,  o relativo a ellos.', 'm. Grupo de lenguas de estos pueblos.'],
