  censo: ['m. Lista de la poblacin o riqueza de un pas o de una comunidad: no estamos incluidos en el ltimo censo.', 'Contrato por el que un inmueble se sujeta al pago de una renta anual.'],
  cear: ['intr. Tomar la cena: a qu hora se cena en esta casa?', 'tr. Comer en ella un determinado alimento: hemos cenado huevos revueltos.'],
