  creo: ['adj. De cera o con sus caractersticas: aspecto creo.'],
  cerio: ['m. qum. Elemento qumico metlico de color gris acero,  que pertenece al grupo de los lantnidos. En forma de xido se usa para pulir y purificar componentes pticos,  y en combinacin,  para hacer ms resistentes las aleaciones de nquel y aluminio. Su smbolo es Ce,   y su nmero atmico,  58.'],
  cerne: ['tr. Pasar por el cedazo cualquier materia en polvo para separar las partes ms finas de las gruesas,  especialmente la harina del salvado; cribar.', 'prnl. Amenazar un mal inminente: la tragedia se cerna sobre ellos.  Irreg. Se conj. como entender.'],
