  cesio: ['m. qum. Elemento qumico metlico,  alcalino,  blando y plateado que se utiliza para fabricar clulas fotoelctricas. Su smbolo es Cs,   y su nmero atmico,  55.'],
  cesta: ['f. Recipiente trenzado de mimbre,  caa o madera flexible: cesta de Navidad,  de la ropa sucia.', 'dep. En baloncesto,  red sin fondo que cuelga del aro por donde debe introducirse el baln; canasta.'],
  cesto: ['m. Cesta grande ms ancha que alta: hemos comprado un cesto de mimbre para guardar los juguetes del nio.'],
