  cetra: ['m. Vara,  bastn o insignia de mando,  generalmente de materiales preciosos,  que exhiban emperadores y reyes como signo de su dignidad.', 'Esta misma dignidad: gran parte de Europa vivi bajo el cetro de los espaoles.'],
  cetro: ['m. Vara,  bastn o insignia de mando,  generalmente de materiales preciosos,  que exhiban emperadores y reyes como signo de su dignidad.', 'Esta misma dignidad: gran parte de Europa vivi bajo el cetro de los espaoles.'],
