  draga: ['f. Mquina que se emplea para limpiar el fondo de los ros,  puertos y zonas navegables de arena,  piedras y otros materiales.', 'Barco que lleva esta mquina.'],
  drago: ['m. rbol agavceo originario de las islas Canarias,  de 12 a 14 m de altura,  con el tronco grueso,  cilndrico,  lleno de cicatrices correspondientes a las hojas perdidas,  la copa recogida y siempre verde y flores pequeas de color blanco verdoso. De su tronco se extrae una resina que se emplea como medicamento: el drago es uno de los smbolos de las islas Canarias.'],
  drama: ['m. Obra literaria de asunto triste,  en verso o en prosa,  que se caracteriza por el empleo exclusivo del dilogo entre los personajes y que est escrita para ser representada en un espacio escnico: un drama de Caldern de la Barca.', 'Gnero literario compuesto por el conjunto de este tipo de obras: drama del siglo xix.'],
