  erbio: ['m. qum. Elemento qumico del grupo de los lantnidos que aparece en forma de polvo metlico gris plateado; es muy raro ya que solo se ha encontrado en Suecia en algunos minerales mezclado con itrio y terbio. Su smbolo es Er,  y su nmero atmico,  68.'],
