  errar: ['tr. e intr. No acertar,  fallar,  equivocarse: el adivino err sus pronsticos.', 'intr. Andar vagando de una parte a otra sin rumbo ni destino:  erraba por las calles sin saber dnde iba a pasar la noche.'],
