  fnix: ['m. Ave mitolgica que renaca de sus cenizas cada vez que mora: el ave fnix se utiliza con frecuencia como smbolo de la persona que se recupera despus de muchas desgracias.', 'Lo que es exquisito o nico en su especie: Lope de Vega,  el fnix de los ingenios.  No vara en pl.'],
  fenol: ['m. qum. Derivado del alquitrn que se usa como antisptico,  como sintetizador de colorantes y en la obtencin de resinas.'],
  feraz: ['adj. [Tierra] muy frtil: la cosecha ser abundante en un terreno tan feraz.'],
  feria: ['f. Mercado extraordinario que tiene lugar en un sitio y unas fechas sealadas:  la feria de Medina del Campo.', 'Fiestas que se celebran en una determinada fecha:  la feria de san Isidro.'],
  feroz: ['adj. Fiero,  aplicado a animales carnvoros que atacan y devoran a sus presas: el lobo feroz.', 'Que causa dao,  terror o destrozo: tormenta feroz.'],
  ferry: ['(voz i.) m. transbordador:  los ferries transportan vehculos en su interior.  pl. ferries.'],
  fetal: ['adj. Del feto o relativo a l: estado fetal.', '[Postura] con las piernas y los brazos encogidos sobre el pecho y la cabeza entre las manos: duerme en posicin fetal.'],
  fetn: ['adj. col. Sincero,  autntico,  verdadero: es un amigo fetn.', 'Bueno,  estupendo,  excelente: ha sido una semana fetn.'],
  feudo: ['m. En la Edad Media,  contrato por el cual los soberanos y los grandes seores concedan tierras u otros bienes a sus vasallos a cambio de que ellos y sus descendientes les prestaran servicios y les jurasen fidelidad: el feudo obligaba a los vasallos a ser fieles a los seores feudales.', 'Tierra o dominio que se concede en feudo: los campesinos medievales explotaban los feudos que les concedan los grandes seores,  pero no eran propietarios de las tierras.'],
  fiado: ['tr. Asegurar uno que cumplir lo que otro promete,  obligndose,  en caso de que no lo haga,  a satisfacer por l: puedes prestarle el dinero,  sus padres le fan.', 'Vender sin cobrar al contado,  aplazando el pago para ms adelante: en esta tienda me fan.'],
  fiana: ['tr. Asegurar uno que cumplir lo que otro promete,  obligndose,  en caso de que no lo haga,  a satisfacer por l: puedes prestarle el dinero,  sus padres le fan.', 'Vender sin cobrar al contado,  aplazando el pago para ms adelante: en esta tienda me fan.'],
  fibra: ['f. Cada uno de los filamentos que entran en la composicin de los tejidos orgnicos vegetales o animales,  de ciertos minerales y de algunos productos qumicos: los esprragos tienen mucha fibra.', 'Filamento obtenido por procedimientos qumicos que se usa principalmente en la industria textil:  prenda de fibra.'],
  ficha: ['f. Pieza pequea,  generalmente plana y delgada,  a la que se puede dar usos diversos (contrasea en guardarropas,  aparcamientos de automviles,  etc.): he dejado el bolso en la consigna y me han dado una ficha.', 'Tarjeta de cartn o papel fuerte en que se consignan ciertos datos y que suele clasificarse:  la ficha de un libro.'],
  ficus: ['m. Nombre genrico de varias plantas morceas entre las que se encuentra la higuera.', 'Planta ornamental de hoja grande y ovalada: tengo un ficus en el saln de mi casa.  No vara en pl.'],
  fideo: ['m. Pasta de harina en hilos cortados que ordinariamente se toma en sopa. Ms en pl.: he preparado una sopa de fideos finos.', 'col. Persona muy delgada: te has quedado hecho un fideo.'],
  fiera: ['f. Animal salvaje,  sobre todo los carnvoros: los leones son fieras.', 'Persona cruel o de carcter malo y violento: no se puede razonar con l,  es una fiera.'],
  fiero: ['adj. De las fieras o relativo a ellas: fiera leona.', 'Feroz,  duro,  agreste o intratable:  paisaje fiero.'],
