  filar: ['tr. col. Darse cuenta de cmo es una persona en realidad,  calar: no me engaa con esa sonrisa,  la he filado.'],
  filfa: ['f. col. Mentira,  engao,  noticia falsa: ese rumor es pura filfa.'],
  filia: ['f. Gran amor o aficin por alguien o algo: filia por la lectura.'],
