  filn: ['m. Masa metalfera o ptrea entre dos capas de un terreno: filn de oro.', 'col. Persona,  negocio o recurso del que se saca o espera sacar gran provecho: esta tienda es un filn.'],
  final: ['adj. Que remata o cierra: acto final.', 'gram. [Proposicin] subordinada adverbial que expresa la finalidad del verbo principal: "Quiero que vengas" es una proposicin adverbial final.'],
  finar: ['intr. pot. Fallecer,  morir.'],
  finca: ['f. Propiedad inmueble en el campo o en la ciudad: ha plantado rboles frutales en su finca.'],
