  finta: ['f. Ademn o amago para engaar,  sobre todo en algunos deportes como el ftbol,  la esgrima o el boxeo: el contrario hizo una finta que le despist.'],
  firma: ['f. Nombre y apellido de una persona que esta pone con rbrica al pie de un escrito: este pasaporte no es vlido,  no tiene tu firma.', 'Acto de firmar: todos estuvieron presentes en la firma del tratado.'],
  firme: ['adj. Estable,  bien sujeto: ese estante no est firme.', 'Que no cambia,  constante:  les una una firme amistad.'],
