  fsio: ["pref. que significa 'naturaleza': fisionoma,  fisioterapia."],
  flaco: ['adj. De pocas carnes,  muy delgado: debes comer ms,  ests demasiado flaco.', 'Flojo,  endeble,  sin fuerzas:  flaco de nimos.'],
  flato: ['m. Acumulacin molesta de gases en el tubo digestivo: el flato me produce dolores abdominales.'],
  fleco: ['m. Adorno compuesto de una serie de hilos o cordoncillos colgantes de una tira de tela. Ms en pl.: los flecos de una colcha.', 'Borde deshilachado por el uso en una tela vieja. Ms en pl.: no te pongas esos pantalones llenos de flecos.'],
  flema: ['f. Mucosidad de las vas respiratorias que se arroja por la boca: se ha atragantado con una flema.', 'Calma,  lentitud,  cachaza: se lo toma todo con demasiada flema.'],
  fleta: ['tr. Alquilar un barco o parte de l para el transporte y,  p. ext.,  alquilar cualquier medio de transporte: fletaron un autobs para el viaje de fin de curso.', 'Embarcar mercancas o personas para su transporte.'],
  fleto: ['tr. Alquilar un barco o parte de l para el transporte y,  p. ext.,  alquilar cualquier medio de transporte: fletaron un autobs para el viaje de fin de curso.', 'Embarcar mercancas o personas para su transporte.'],
  flexo: ['m. Lmpara de mesa con un brazo flexible que permite concentrar la luz en un espacio determinado: tiene un flexo en su mesa de estudio.'],
  flipe: ['m. col. Lo que gusta mucho: esa pelcula es un flipe.', 'col. Estado del que se ha drogado: no se entera de nada por el flipe que tiene.'],
  flojo: ['adj. Poco apretado,  ajustado o tirante:  este clavo est flojo.', 'Que no tiene mucha fortaleza o vigor:  la enfermedad le ha dejado flojo.'],
  flora: ['f. Conjunto de plantas de una zona: flora de una regin.', 'Conjunto de bacterias que habitan en un rgano determinado,  y cuya presencia es indispensable para el buen funcionamiento del organismo: flora intestinal.'],
  flota: ['f. Conjunto de barcos mercantes o de guerra de un pas,  compaa de navegacin,  lnea martima,  etc.: flota armada,  pesquera.', 'Conjunto de vehculos de una empresa,  pas,  etc.:  flota de taxis,  de camiones.'],
  flote: ['m. flotacin.', 'a flote loc. adv. Mantenindose sobre el agua: el barco se mantuvo a flote.'],
  fluir: ['intr. Correr un lquido o un gas: fluir un ro.', 'Surgir algo con facilidad: fluir las palabras,  los pensamientos.'],
  flujo: ['m. Accin y resultado de fluir los lquidos y los gases: han cortado el flujo de agua.', 'Abundancia excesiva: flujo de capitales.'],
  flor: ['m. qum. Elemento qumico gaseoso de olor desagradable y color amarillo verdoso que se emplea como reactivo qumico. Su smbolo es F,  y su nmero atmico,  9:  el flor es un componente bsico en los dentfricos.'],
  focal: ['adj. Del foco o relativo a l: distancia focal.'],
  focha: ['f. Nombre comn de varias aves gruiformes de unos 40 cm de longitud,  nadadoras,  de plumaje negro con reflejos grises y pies de color verdoso amarillento.'],
  fogn: ['m. Antiguamente,  sitio en las cocinas donde se haca el fuego para guisar.', 'En las calderas de las mquinas de vapor,  lugar destinado al combustible: hay que echar carbn en el fogn para que funcione la locomotora.'],
  fola: ['f. Canto y baile popular de las islas Canarias.', 'pl. Baile portugus que se ejecutaba entre muchas personas.'],
  folio: ['m. Hoja de un libro,  cuaderno,  etc.: el informe consta de cincuenta folios.', 'Hoja de papel cuyo tamao corresponde a dos cuartillas: hay que aadirle folios a la impresora.'],
  folla: ['tr. vulg. Tener relaciones sexuales. Tambin intr. y prnl.'],
  fonda: ['f. Establecimiento pblico donde se da alojamiento y se sirven comidas.'],
  fondo: ['m. Parte inferior de una cosa: el fondo del vaso.', 'Hablando del mar,  de los ros o estanques,  superficie slida sobre la cual est el agua: el agua era tan cristalina que podan verse los corales en el fondo.'],
  foque: ['m. mar. Cada una de las velas triangulares de una embarcacin: arriar el foque.'],
  foral: ['adj. Del fuero o relativo a l: derecho foral.'],
  foras: ['m. Plaza de las ciudades de la antigua Roma donde se trataban los negocios pblicos y se celebraban los juicios: en las ruinas de la ciudad se distinguan el foro y las calles principales.', 'Coloquio,  debate sobre asuntos de inters ante un auditorio: un foro sobre la droga.'],
  forja: ['f. Accin y resultado de forjar: forja de una herradura,  de un hroe.', 'Taller donde se trabaja el metal: me han hecho unas herraduras para el caballo en una forja.'],
