  foro : ['m. Plaza de las ciudades de la antigua Roma donde se trataban los negocios pblicos y se celebraban los juicios: en las ruinas de la ciudad se distinguan el foro y las calles principales.', 'Coloquio,  debate sobre asuntos de inters ante un auditorio: un foro sobre la droga.'],
  forro: ['m. Cubierta,  resguardo o revestimiento de algo,  especialmente referido al que llevan los vestidos en la parte interior: la chaqueta lleva un forro de raso.', 'Cubierta del libro: forro de vitela.'],
