  fosco: ['adj. [Pelo] alborotado y fuerte: mi pelo es fosco y difcil de peinar.', 'Oscuro: ambiente fosco.'],
  fsil: ['adj. [Resto de un ser] orgnico muerto que se encuentra petrificado en ciertas capas terrestres. Tambin m.: encontramos el fsil de un trilobites.', 'col. Viejo,  anticuado. Tambin com.: en aquel caf se renen todos los fsiles de la localidad.'],
  fotn: ['m. fs. Partcula de luz que se propaga en el vaco: los rayos luminosos estn formados por flujos de fotones.'],
  fraga: ['f. Terreno escarpado y poblado de maleza: el zorro se ocult en la fraga y los cazadores no pudieron seguirlo.'],
  frase: ['f. Conjunto de palabras que tienen un sentido: un sujeto y un predicado forman una frase.', 'Locucin,  expresin:  frase proverbial.'],
  frer: ['tr. Cocinar un alimento en aceite o grasa hirviendo: he frito huevos para todos. Tambin prnl.', 'Molestar,  importunar:  le frieron los mosquitos.'],
  freno: ['m. Dispositivo para moderar o detener el movimiento de algunas mquinas,  vehculos,  etc.: pisa el freno,  que vas demasiado rpido.', 'Instrumento de hierro que,  introducido en la boca de las caballeras,  sirve para sujetarlas y dirigirlas.'],
  fren: ['m. qum. Fluido no inflamable que contiene flor y que se emplea como propelente para aerosoles,  como refrigerante,  etc.: los freones son txicos si se inhalan.'],
  fresa: ['f. Planta roscea con tallos rastreros,  flores blancas o amarillentas y fruto casi redondo,  de color rojo y de 1 cm de largo.', 'Fruto de esta planta: la primavera es la temporada de las fresas.'],
