  freza: ['f. Desove,  puesta de los huevos y periodo de tiempo en que ocurre: est prohibido pescar durante la freza; el salmn emigra a los ros para la freza.', 'Conjunto de huevos de un pez o conjunto de peces recin salidos de ellos: la freza del esturin se conoce como caviar.'],
