  frita: ['p. p. irreg. de frer.', 'adj. col. Muy dormido: estar o quedarse frito.'],
  frito: ['p. p. irreg. de frer.', 'adj. col. Muy dormido: estar o quedarse frito.'],
  frote: ['m. frotamiento.'],
  fruta: ['f. Fruto comestible de ciertas plantas: la fruta aporta muchas vitaminas al organismo.'],
  fruto: ['m. bot. rgano de la planta que nace del ovario de la flor y que contiene las semillas.', 'Resultado,  provecho,  utilidad: sus esfuerzos han dado fruto.'],
  fuego: ['m. Calor y luz producidos por la combustin: aviva el fuego.', 'Materia en combustin:  dame fuego.'],
  fuera: ['adv. l. En o hacia la parte exterior: te espero fuera.', 'adv. t. Antes o despus de tiempo:  lo present fuera de plazo.'],
  fuero: ['m. Privilegio,  derecho,  exencin,  etc.,  que se conceden a una persona,  ciudad o territorio. Ms en pl.: la regin no quiere perder sus fueros.', 'En la Edad Media,  ley o estatuto concedido por un soberano a un territorio: el rey otorg un fuero a la ciudad.'],
  fuete: ['m. Esencia y naturaleza: el ser de los animales es la subsistencia y la procreacin.', 'Vida, existencia: dar el ser.'],
  fugaz: ['adj. De corta duracin: visita fugaz.', 'Que desaparece rpidamente:  estrella fugaz.'],
  fular: ['m. Pauelo para el cuello o bufanda de seda u otro tejido fino.  Tambin se utiliza la forma original francesa foulard.'],
  fulla: ['(voz i.) m. En el juego de pker,  combinacin de una pareja y un tro: nos gan con un full de ases y reyes.'],
  fumar: ['intr. Aspirar y despedir el humo del tabaco,  opio,  etc.: fumar perjudica la salud. Tambin tr.: fuma rubio.', 'prnl. col. Consumir,  gastar:  se ha fumado todo el sueldo.'],
  funda: ['f. Cubierta con que se envuelve o cubre algo: la funda de las tijeras,  de las gafas.'],
  fundo: ['tr. Establecer,  crear: los fenicios fundaron Cartago.', 'Apoyar con razones,  pruebas,  etc. Tambin prnl.:  se fund en documentos de la poca.'],
