  furor: ['m. Clera,  ira exaltada: el furor asomaba a sus ojos.', 'Prisa,  vehemencia: acometi el trabajo con furor.'],
  fusil: ['m. Arma de fuego porttil con un can largo,  destinada al uso de los soldados de infantera: los infantes caminaban con sus fusiles al hombro.'],
  fsil: ['m. Arma de fuego porttil con un can largo,  destinada al uso de los soldados de infantera: los infantes caminaban con sus fusiles al hombro.'],
