  fuste: ['m. arquit. Parte de la columna que media entre el capitel y la basa: las columnas jnicas tienen el fuste estriado.', 'Armazn de la silla de montar.'],
  ftil: ['adj. De poca importancia,  insignificante: asunto ftil.'],
  futn: ['m. Colchoneta plegable que se pone en un tatami o directamente sobre el suelo y se usa como cama: los futones son de origen japons.'],
