  gacha: ['f. Cualquier masa muy blanda y lquida.', 'pl. Comida compuesta de harina cocida con agua y sal,  que se puede aderezar con leche,  miel,  etc.: las gachas solan ser comida de gentes de campo.'],
  gach: ['f. col. Mujer,  generalmente joven: he visto una gach guapsima en el parque.  pl. gachs.'],
  gach: ['m. col. Hombre en general,  y en especial el novio o amante de una mujer: paseaba del brazo de un gach.'],
  gacho: ['adj. Encorvado,  inclinado hacia la tierra: ir con la cabeza gacha.'],
  gafar: ['tr. col. Dar o traer mala suerte: la presencia del ministro gaf al equipo nacional y perdieron el partido.'],
  gaita: ['f. Instrumento musical de viento con varios tubos unidos a un fuelle: la gaita es tpica de Galicia.', 'Instrumento musical de viento en forma de tubo con agujeros,  propio de las fiestas populares: en la plaza habr msicos tocando la gaita y el tamboril.'],
  galn: ['m. Hombre apuesto y bien parecido: ese galn las enamora a todas.', 'Actor principal que interpreta papeles de tipo amoroso: el galn de la pelcula siempre acaba con la protagonista.'],
  gals: ['adj. y s. De Gales o relativo a este pas del Reino Unido.', 'm. Lengua cltica de esta regin.'],
