  galgo: ['adj. y s. [Perro] de hocico y rabo largos,  muy veloces,  que se utilizan para cazar y en carreras: los galgos necesitan mucho ejercicio.'],
  galio: ['m. qum. Elemento qumico metlico blanco,  duro y maleable,  parecido al aluminio. Su smbolo es Ga,  y su nmero atmico,  31:  el galio se suele encontrar en los minerales de cinc.'],
  galla: ['m. Ave galliforme de cresta roja y alta,  pico corto,  grueso y arqueado,  y unas formaciones carnosas rojas colgantes bajo el pico. Tiene abundante y vistoso plumaje y un espoln en cada tarso: en cuanto amanece se oye cantar a los gallos.', 'Pez ostectio pleuronectiforme marino,  con el cuerpo muy comprimido,  comestible,  parecido al lenguado: gallo a la plancha.'],
  gallo: ['m. Ave galliforme de cresta roja y alta,  pico corto,  grueso y arqueado,  y unas formaciones carnosas rojas colgantes bajo el pico. Tiene abundante y vistoso plumaje y un espoln en cada tarso: en cuanto amanece se oye cantar a los gallos.', 'Pez ostectio pleuronectiforme marino,  con el cuerpo muy comprimido,  comestible,  parecido al lenguado: gallo a la plancha.'],
  galn: ['m. Cinta estrecha y fuerte de seda o de hilo plateado o dorado que se usa como adorno o para hacer ribetes: el pantaln lleva dos galones a lo largo de las perneras.', 'Cinta parecida con la que se indica la graduacin en los uniformes militares: galones de teniente.'],
  gamba: ['f. Nombre comn de diversos crustceos decpodos comestibles menores que el langostino: gambas a la plancha.'],
  gamma: ['f. Tercera letra del alfabeto griego,  que se corresponde con nuestra g. Su grafa mayscula es G y la minscula ?.'],
  gamn: ['m. Planta lilicea con hojas erguidas y largas en figura de espada,  flores blancas con una lnea rojiza en cada ptalo y races tuberculosas,  cuyo cocimiento se ha empleado para la curacin de enfermedades cutneas.'],
  ganar: ['tr. Obtener un beneficio:  gan varios millones en la lotera.', 'Recibir un jornal o sueldo por un trabajo habitual: al principio no ganaba mucho,  pero pronto ascendi.'],
  ganga: ['f. Ventaja o cosa que se consigue sin esfuerzo o por poco dinero: el coche ha sido una autntica ganga.', 'Ave galliforme de forma y tamao semejantes a los de la perdiz; tiene la garganta negra,  un lunar rojo en la pechuga y el resto del cuerpo negro,  pardo y blanco.'],
  gansa: ['m. y f. Nombre comn de diversas aves anseriformes resultantes de la domesticidad del nsar,  de plumaje gris pardo,  pico anaranjado grueso y pies rojizos. Menos acuticas que el pato,  se cran bien en pases hmedos y son apreciadas por su carne y por su hgado,  con el que se fabrica el foie-gras:  el ganso grazna fuertemente al menor ruido,  y por ello se le ha considerado como animal guardin.', 'adj. y s. [Persona] torpe,  incapaz: es un ganso y no hace ms que frenar el trabajo del equipo.'],
  ganso: ['m. y f. Nombre comn de diversas aves anseriformes resultantes de la domesticidad del nsar,  de plumaje gris pardo,  pico anaranjado grueso y pies rojizos. Menos acuticas que el pato,  se cran bien en pases hmedos y son apreciadas por su carne y por su hgado,  con el que se fabrica el foie-gras:  el ganso grazna fuertemente al menor ruido,  y por ello se le ha considerado como animal guardin.', 'adj. y s. [Persona] torpe,  incapaz: es un ganso y no hace ms que frenar el trabajo del equipo.'],
  gan: ['m. Mozo de labranza.', 'Hombre basto: tiene modales de gan en la mesa.'],
  gair: ['intr. Aullar el perro y otros animales con gritos agudos y repetidos cuando los maltratan: el perro gaa cuando vea el bastn de su amo.', 'Graznar las aves.  Irreg. Se conj. como mullir.'],
  garba: ['m. Elegancia,  desenvoltura al andar y moverse: con qu garbo se mueve esa muchacha!', 'Gracia.'],
  garbo: ['m. Elegancia,  desenvoltura al andar y moverse: con qu garbo se mueve esa muchacha!', 'Gracia.'],
  garra: ['f. Pata de un animal cuando tiene uas curvas y fuertes: la leona clav sus garras a la gacela.', 'Mano de una persona: qutame las garras de encima!'],
  garza: ['f. Nombre comn de diversas aves ciconiformes cuya altura vara de 80 cm a 1,5 m segn la especie. Tienen el pico largo,  la cabeza pequea y el cuello largo,  con el plumaje gris claro,  blanco o pardo. Viven a orillas de los ros y pantanos y se alimentan de peces.', 'garza real Garza que tiene un moo largo,  negro y brillante,  el dorso azulado,  vientre y pecho blancos,  con las alas grises,  las plumas mayores negras,  tarsos verdosos y pico largo y amarillo que se va oscureciendo hacia la punta. Abunda en las zonas hmedas de la pennsula Ibrica.'],
  garzo: ['adj. De color azulado,  especialmente referido a los ojos de este color y a las personas que los tienen as: joven de mirada garza.'],
  gasto: ['m. Accin de gastar: la boda ha supuesto un gran gasto.', 'Lo que se gasta o se ha gastado: apunta en esta libreta los ingresos y los gastos.'],
  gatas: ['m. y f. Mamfero carnvoro domstico de cabeza redonda,  lengua muy spera,  patas cortas armadas de uas fuertes,  agudas y retrctiles,  pelaje espeso y suave de diversos colores y ojos cuya pupila se dilata para ver en la oscuridad: mi gato es muy carioso.', 'col. Madrileo: nac en Madrid,  as que soy gata.'],
  gateo: ['intr. Andar a gatas:  el nio ya gatea.', 'Trepar como los gatos:  gate al rbol para coger el baln.'],
  gavia: ['f. Vela que se coloca en el mastelero mayor de las naves.', 'P. ext.,  cada una de las velas correspondientes en los otros dos masteleros.'],
  gemir: ['intr. Expresar pena y dolor con sonido y voz lastimera: deja de gemir y haz algo para solucionar tus problemas.', 'Aullar algunos animales,  o sonar algunas cosas inanimadas,  con semejanza al gemido del hombre: el perro gema a la puerta de la casa.  Irreg. Se conj. como pedir.'],
  genio: ['m. Carcter,  modo de ser de alguien: tiene un genio muy vivo; es una persona de genio alegre.', 'Humor,  estado de nimo:  hoy est de mal genio.'],
