  glosa: ['f. Explicacin,  comentario de un texto: los primeros textos en castellano fueron unas glosas escritas en un manuscrito en latn.', 'Nota explicativa en un libro de cuentas: las glosas del contable estaban escritas con letra menuda.'],
  glose: ['tr. Hacer,  poner o escribir glosas: glosaron el texto para facilitar su comprensin.', 'Comentar palabras y dichos propios o ajenos,  amplindolos: los periodistas glosaron el breve comentario del ministro.'],
