  gobio: ['m. Nombre comn de diversos peces ostectios perciformes marinos de pequeo tamao,  con las aletas abdominales colocadas debajo de las torcicas y unidas ambas por los bordes formando una ventosa: los gobios abundan en aguas litorales.', 'Pez ostectio cipriniforme de unos 15 cm,  con dos barbillas cortas en la boca,  manchas en el lomo y cuerpo cilndrico,  que vive en los ros donde forma bancos: los gobios viven en Europa y en el norte de Asia.'],
