  gordo: ['adj. De mucha carne o grasa: el nio est muy gordo para su edad. Tambin s.:era el gordo de la clase.', 'Voluminoso,  grueso:  tiene que leerse un libro as de gordo en dos das.'],
  gorra: ['f. Prenda para cubrir la cabeza,  sin copa ni alas,  que suele llevar visera: ponte la gorra para que no te de una insolacin.', 'de gorra loc. adv. col. A costa ajena,  gratis: fue de gorra al concierto.'],
  gorro: ['m. Prenda de tela o lana para cubrir y abrigar la cabeza: lleva un gorro de lana para esquiar.', 'gorro de bao El de material impermeable para cubrirse el pelo durante el bao: en la piscina cubierta hay que utilizar gorro de bao.'],
  goteo: ['m. Cada gota a gota de un lquido: el goteo constante horad la superficie piedra.', 'Lo que se da o recibe espaciadamente y en pequea cantidad: el material escolar supone un continuo goteo de gastos.'],
  gozar: ['tr. Poseer algo material o inmaterial. Tambin como intr. con la prep. de:  goza de gran fortuna.', 'intr. Sentir placer,  disfrutar con algo:  goza con los nios. Tambin prnl.'],
  gozne: ['m. Herraje articulado que fija las hojas de una puerta o ventana y permite que se abatan: los goznes del portn chirriaron.', 'Bisagra metlica: ha tenido que cambiar los goznes del bal.'],
