  gramo: ['m. Unidad de masa en el sistema mtrico decimal,  equivalente a la milsima parte de un kilogramo: un gramo equivale a un centmetro cbico de agua destilado a cuatro grados centgrados.', 'Unidad de fuerza o peso equivalente a la fuerza con la que una masa de un gramo es atrada por la Tierra.'],
  grana: ['f. Formacin y crecimiento del grano de los frutos: el pedrisco arruin las vides en grana.', 'Tiempo en que ocurre: la grana se ha adelantado con las lluvias.'],
