  helio: ['m. qum. Elemento qumico inerte,  gaseoso,  incoloro,  inodoro,  inspido y el ms ligero de todos los cuerpos,  despus del hidrgeno. Su smbolo es He,  y su nmero atmico,  2:  el helio se obtiene en estado puro por destilacin fraccionada del aire lquido.'],
