  morro: ['m. Hocico de los animales.', 'vulg. Labio: nio, lmpiate esos morros!  Puede usarse en pl. con significado de sing.'],
  morsa: ['f. Mamfero pinnpedo acutico de unos 3,50 m de longitud que se caracteriza por poseer dos grandes colmillos que se prolongan fuera de la mandbula en el macho: las morsas viven en los mares rticos frente a las costas y se alimentan de pequeos invertebrados.'],
