  mun: ['m. Parte de un miembro cortado que permanece adherido al cuerpo: le ha quedado un mun a la altura del codo despus del accidente.', 'Parte del cuerpo que nace atrofiada y no se desarrolla: naci con muones en vez de pies.'],
