  musgo: ['m. Cada una de las plantas briofitas, con hojas provistas de pelos absorbentes, que crecen abundantemente sobre las piedras, cortezas de rboles, el suelo y otras superficies sombras: el musgo suele crecer mirando al norte.', 'Conjunto de estas plantas que cubren una determinada superficie: la piedra estaba llena de musgo.'],
