  narra: ['tr. Contar una historia o suceso, real o imaginario, oralmente, por escrito o de cualquier otra manera: este cuento narra lo que le sucedi a una nia que se meti en un agujero siguiendo a un extrao conejo.'],
  nasal: ['adj. Relativo a la nariz: cavidad nasal.', '[Sonido] en cuya pronunciacin la corriente espirada sale total o parcialmente por la nariz: est emitiendo sonidos nasales todo el rato por el catarro que tiene.'],
