  nobel: ['m. Cada uno de los premios creados por Alfred Nobel, que se conceden anualmente a personas o instituciones que hubieran destacado en los campos de la Medicina y Fisiologa, Qumica, Fsica, Ciencias Econmicas, Literatura y de la Paz: el nobel de Literatura de 1998 es el portugus Jos Saramago.  No vara en pl.'],
