  nutra: ['tr. y prnl. Proporcionar a un organismo vivo las sustancias que necesita para realizar sus funciones.', 'Fortalecer, vigorizar, alentar: a menudo, la vida se nutre de ilusiones.'],
  oasis: ['m. Zona con vegetacin y agua que se encuentra aislada en los desiertos arenales de frica y Asia.', 'Tregua, descanso: tras la discusin vivimos un oasis de paz.  No vara en pl.'],
  obeso: ['adj. y s. [Persona] excesivamente gruesa.'],
  bice: ['m. Obstculo, impedimento: que no le guste madrugar no es bice para que le encante ir a la escuela.  Suele usarse en frases negativas.'],
  bito: ['m. Fallecimiento de una persona.'],
  oblea: ['f. Hoja delgada de pan zimo de la que se sacan las hostias y las formas.', 'Cada uno de estos trozos.'],
  bolo: ['m. Pequea suma de dinero con la que se contribuye a un fin determinado.', 'Moneda de plata de los antiguos griegos.  No confundir con volo.'],
  obrar: ['tr. Llevar a cabo una accin, hacerla de determinada manera.', 'Construir, edificar, hacer una obra.'],
  obvio: ['adj. Evidente, muy claro o que no tiene dificultad: es obvio que lo hizo por rencor.'],
  ocapi: ['m. okapi.'],
  ocaso: ['m. Puesta del Sol o de otro astro.', 'Occidente, punto cardinal.'],
  ocelo: ['m. zool. Cada ojo simple de los que forman un ojo compuesto de los artrpodos, mediante el cual perciben la luz, pero no la imagen de los objetos.', 'Mancha redonda y bicolor en las alas de algunos insectos o en las plumas de ciertas aves.'],
  oden: ['m. Teatro o lugar destinado en la antigua Grecia a los espectculos musicales.', 'P. ext., teatro moderno, y,en especial, el destinado a representar peras.'],
  odiar: ['tr. Sentir odio o aversin por alguien o por algo.  Se conj. como cambiar.'],
  oeste: ['m. Occidente, punto cardinal. Su abreviatura es O.  Se escribe con mayscula.', 'Viento que sopla de all.'],
  ofita: ['f. Roca eruptiva plutnica compuesta de feldespato, piroxeno y caliza o cuarzo, con color y textura variables, que se emplea en la fabricacin de objetos ornamentales.'],
  ohmio: ['m. Unidad de resistencia elctrica en el Sistema Internacional de Medidas, equivalente a la resistencia elctrica que da paso a una corriente de un amperio cuando entre sus extremos existe una diferencia de potencial de un voltio. Su smbolo es O.'],
