  pagar: ['tr. Dar a alguien el dinero que se le debe o le corresponde: pagar a un acreedor.', 'Cumplir el castigo por un delito o falta, o sufrir las consecuencias de algo malo que se ha hecho: pagar las culpas.'],
  pagel: ['m. breca.'],
  pagro: ['m. Pez ostectio perciforme de unos 75 cm de longitud, con el dorso rosa y los flancos plateados, carnvoro y de carne apreciada:   los pagros viven en el Atlntico tropical hasta Gran Bretaa, siendo poco frecuentes en el Mediterrneo.'],
  pairo: ['m. mar. Modo de estar un barco, quieto y con las velas extendidas.'],
  pajar: ['m. Sitio cubierto donde se guarda la paja.'],
  pajea: ['m. Criado joven que acompaaba a sus amos o serva como criado en la casa: un paje del conde llev el mensaje.', 'Mueble formado por un espejo con pie alto y una mesilla para utensilios de tocador.'],
  pajel: ['m. pagel.'],
  pajn: ['m. Caa alta y gruesa de las tierras que han quedado de rastrojo.', 'amer. Gramnea silvestre, muy nutritiva, que sirve de alimento al ganado.'],
  palca: ['m. Habitacin independiente a modo de balcn, con varios asientos, que hay en teatros y en otros espectculos: reservaron un palco para toda la familia.', 'Pequeo tablado donde se coloca la gente para ver un espectculo.'],
  palco: ['m. Habitacin independiente a modo de balcn, con varios asientos, que hay en teatros y en otros espectculos: reservaron un palco para toda la familia.', 'Pequeo tablado donde se coloca la gente para ver un espectculo.'],
  palia: ['tr. Disminuir la intensidad de un dolor o los efectos dainos de algo: paliar la sequa, el dolor.', 'Mitigar, suavizar, atenuar, quitarle importancia a algo: paliar un escndalo.  Tiene dos modelos de conj., uno como vaciar y otro como cambiar.'],
  palio: ['m. Dosel rectangular de rica tela que, colocado sobre cuatro o ms varas largas, se utiliza en ciertos actos religiosos para cubrir con l al sacerdote que lleva las hostias consagradas, a una imagen o a algunas personalidades, como el Papa.', 'cin. Reflector metlico que se utiliza para la iluminacin de exteriores.'],
  palma: ['f. Nombre comn de las plantas de la familia de las palmceas.', 'rbol de la palmera.'],
  palmo: ['m. Medida de longitud equivalente a unos 21 cm, que es aproximadamente la distancia que existe entre el dedo pulgar y el meique con la mano extendida.', 'dejar a uno con un palmo de narices loc. col. Decepcionarlo, chasquearlo, privndolo de lo que esperaba conseguir.'],
