  pampa: ['f. Llanura extensa de Amrica meridional sin vegetacin arbrea.'],
  panal: ['m. Conjunto de celdillas de cera que las abejas forman dentro de la colmena para depositar la miel.', 'Estructura semejante que fabrican las avispas y otros animales.'],
  panda: ['f. Grupo de amigos: he ido al cine con mi panda.', 'Pandilla que forman algunos para hacer dao.'],
  pando: ['adj. Que se tuerce: estante pando.', 'Que se mueve lentamente, como los ros cuando van por tierra llana.'],
  panel: ['m. Cada una de las piezas o separaciones en que se divide una pared, la hoja de una puerta u otra superficie.', 'Elemento prefabricado para hacer divisiones en los edificios: han puesto paneles en mi oficina para separar los despachos.'],
  panti: ['m. Leotardo de seda, nailon o material semejante que utilizan las mujeres para cubrirse las piernas. Ms en pl.: pantis de seda.  pl. pantis.'],
  panty: ['(voz i.) m. panti.'],
  panza: ['f. Barriga, vientre de las personas, sobre todo cuando es prominente:  tiene una panza cervecera de tamao considerable.', 'Parte ms saliente de algunos recipientes u objetos: panza de una jarra.'],
  paal: ['m. Trozo de tela o de un material absorbente que se pone a los bebs como si fuera una braga para empapar la orina.', 'en paales loc. adv. En un estado de poco desarrollo, en el principio: el proyecto an est en paales.'],
  paol: ['m. mar. panol.'],
  papal: ['adj. Del Papa o relativo a este cargo mximo de la Iglesia catlica: viaje, discurso papal.'],
  papel: ['m. Material hecho con pasta vegetal molida y blanqueada que se dispone en finas lminas y se usa para escribir, dibujar, etc.', 'Hoja o trozo de este material: dame un papel para anotar su telfono.'],
  papeo: ['m. col. Comida: es la hora del papeo.'],
  papa: ['adj. y com. De la Papuasia o relativo a esta regin melanesia de Nueva Guinea y de las islas Fiji.', 'm. Lengua hablada por los papes.  pl. papes.'],
  parar: ['intr. Cesar o interrumpirse un movimiento o accin. Tambin prnl.: el tren se par; no paraba de comer.', 'Llegar a cierto estado, condicin, etc., despus de haber pasado por distintos sucesos: el escrito fue a parar a la mesa del director.'],
  parca: ['f. mit. Cada una de las tres deidades infernales hermanas, Cloto, Lquesis y tropos, representadas con figura de viejas, de las cuales la primera hilaba, la segunda devanaba y la tercera cortaba el hilo de la vida del hombre.', 'pot. La muerte: cuando la parca vino a visitarlo.'],
  parco: ['adj. Sobrio y moderado en cualquier aspecto: es parco en la bebida.'],
  pardo: ['adj. De color marrn rojizo: oso pardo. Tambin m.:el pardo de sus ojos.', 'Oscuro: da pardo.'],
  pared: ['f. Obra de albailera levantada en posicin vertical para cerrar un espacio o sostener el techo: paredes de un edificio.', 'Tabique: tir las paredes para hacer ms grande el saln.'],
  pareo: ['m. Emparejamiento, unin.'],
  paria: ['com. Persona de la casta nfima de los hindes: los parias apenas tienen derechos.', 'Persona insignificante: es un paria en su empresa.'],
  pario: ['intr. Expulsar la hembra el feto que tena en su vientre. Tambin tr.: la perra pari tres cachorros.', 'tr. Producir o causar una cosa.'],
  parir: ['intr. Expulsar la hembra el feto que tena en su vientre. Tambin tr.: la perra pari tres cachorros.', 'tr. Producir o causar una cosa.'],
  pars: ['intr. Expulsar la hembra el feto que tena en su vientre. Tambin tr.: la perra pari tres cachorros.', 'tr. Producir o causar una cosa.'],
  parka: ['f. Chaquetn de abrigo con capucha e impermeable.'],
  parla: ['intr. Hablar, conversar mucho y sin sustancia.', 'Hacer algunas aves sonidos que se asemejan a la locucin humana.'],
  parn: ['m. col. Dinero.'],
  parra: ['f. Vid de tronco leoso, sobre todo si est levantada sobre un armazn artificial por la que extender sus ramas.', 'subirse uno a la parra loc. col. Tomarse alguno atribuciones o confianzas que no le corresponden: si sigues dndole confianzas se subir a la parra.'],
  parsi: ['com. Pueblo procedente de la antigua Persia y seguidor de la religin de Zoroastro, que habita actualmente en la India.', 'm. Idioma hablado por los parsis: el parsi es distinto del snscrito.'],
  parte: ['f. Porcin indeterminada de un todo: parte de la casa, del cuerpo.', 'Porcin que le corresponde a alguien en un reparto, cuota, etc.: partes de una herencia.'],
  parto: ['m. Salida del feto del cuerpo materno dando por finalizado el embarazo: parto sin dolor.', 'Cualquier produccin fsica o intelectual: parto de una novela.'],
  parva: ['f. Mies extendida en la era para trillarla.'],
  parvo: ['adj. Pequeo, escaso: parvo y frugal desayuno.'],
  pasar: ['tr. Llevar, trasladar, conducir de un lugar o situacin a otro: pas de aprendiz a oficial en un ao. Tambin intr. y prnl.:pasarse al enemigo.', 'Cruzar de una parte a otra: pasar la frontera, un ro.'],
  paseo: ['m. Accin de pasear: dar un paseo; paseo en bicicleta, a caballo.', 'Lugar o sitio pblico para pasearse: paseo martimo.'],
  pasma: ['f. argot Polica: la pasma hizo una redada.'],
  pasmo: ['m. Admiracin y asombro extremos, que dejan como paralizado.', 'Efecto de un enfriamiento que se manifiesta por catarro, dolor de huesos y otras molestias.'],
  pasta: ['f. Masa moldeable hecha con cualquier material: pasta de escayola.', 'Masa de harina con manteca o aceite que sirve para preparar tartas, empanadas, etc.: pasta de hojaldre.'],
  paste: ['tr. Conducir el ganado a los prados para que coma.', 'intr. Comer el ganado hierba en los prados: hay varias vacas pastando en el prado.'],
  pasto: ['m. Accin de pastar.', 'Hierba que come el ganado en el mismo terreno donde se cra.'],
  patn: ['m. Hombre zafio y ordinario. Tambin adj.', 'Aldeano, hombre rstico.'],
  patas: ['f. Extremidad anterior y posterior de los animales: el caballo se rompi la pata.', 'Pie de un mueble: las sillas tienen cuatro patas.'],
  pateo: ['m. Accin y resultado de patear, por enojo o enfado: el pateo del pblico era ensordecedor.'],
  pter: ['m. Sacerdote, sobre todo aquel cuyos feligreses son militares: el pter del cuartel charlaba con los soldados.', 'pter familias En la antigua Roma, cabeza de familia, persona que ejerca la potestad en ella.  No vara en pl.'],
  patn: ['m. Plancha que se adapta a la suela del calzado, o que va incorporada a una bota, provista de una especie de cuchilla o de ruedas, segn sirva para ir sobre el hielo o sobre un pavimento duro, liso y muy llano: hockey sobre patines.', 'patinete.'],
  patio: ['m. Espacio limitado por paredes o galeras, que en las casas y otros edificios se deja al descubierto.', 'patio de butacas:  localidades de patio.'],
  pausa: ['f. Breve interrupcin de un movimiento, proceso, accin, etc.: hacemos una pausa en el trabajo para desayunar.', 'Tardanza, lentitud: hablar con pausa.'],
  pauta: ['f. Regla para hacer rayas paralelas en un papel y evitar torcerse al escribir.', 'Conjunto de rayas horizontales hechas con esta regla.'],
