  pulpo: ['m. Nombre comn de diversas especies de moluscos cefalpodos que tienen el cuerpo en forma de saco, con ocho brazos de gran longitud, y poseen un sifn por el que expulsan agua para propulsarse o tinta para enturbiar el agua cuando se sienten amenazados; su carne es apreciada como comestible: pulpo a la gallega.', 'Cinta elstica terminada en ganchos metlicos por ambos lados que sirve para sujetar objetos: asegur el colchn en la baca del coche con unos pulpos.'],
