  venda: ['f. Banda,  tira de gasa o tela usada para sujetar una zona del cuerpo o cubrir una herida o apsito: la venda estaba tan fuerte que le cortaba la circulacin.', 'tener alguien una venda en los ojos loc. Desconocer la verdad o la realidad por ofuscamiento.'],
  vend: ['tr. Ceder la propiedad de algo por un precio convenido: ha vendido su chalet en la sierra.', 'Delatar,  traicionar la confianza de alguien: su cmplice le vendi a la polica.'],
