  venir: ['intr. Trasladarse o llegar hasta donde est el que habla:  vino a Cdiz ayer. Tambin prnl.:vente conmigo al cine.', 'Llegar el tiempo en que algo va a suceder:  ya vienen las Navidades!'],
  venta: ['f. Cesin de la propiedad de algo a cambio de un precio establecido: hoy firman la venta de sus acciones.', 'Cantidad de cosas que se venden: la venta de coches ha bajado este mes.'],
  venus: ['m. astron. Segundo planeta del sistema solar que presenta un resplandor intenso y tiene fases similares a las de la Luna.  Se escribe con mayscula: Venus es un planeta similar en tamao a la Tierra.', 'f. mit. Diosa del amor y de la belleza en la mitologa romana.  Se escribe con mayscula: Venus naci de la espuma del mar.'],
  veras: ['f. pl. Lo que es verdadero y cierto: lo dijo entre veras y bromas y no lo tomamos en serio.', 'de veras loc. adv. Con verdad: te lo digo de veras,  creme.'],
  veraz: ['adj. Verdadero:  noticia veraz.', 'Que habla o acta de acuerdo con la verdad: confidente veraz.'],
