  verbo: ['m. gram. Parte conjugable de la oracin que expresa la accin y el estado del sujeto y ejerce la funcin sintctica de ncleo del predicado: el verbo de esta oracin es compuesto.', 'Palabra: siempre busca el verbo ms apropiado.'],
  verde: ['adj. De color semejante al de la hierba fresca o a la esmeralda: se compr un vestido verde en las rebajas. Tambin m.', '[rbol o planta] que no est seco: el rosal todava est verde y podr florecer la prxima primavera.'],
