  vichy: ['(voz fr.) m. Tela fuerte de algodn de rayas o cuadros: la tela de vichy se usa para confeccionar batas,  babis y prendas similares.'],
  vicia: ['tr. Daar o corromper fsica o moralmente: sus amigos le estn viciando con el alcohol. Tambin prnl.', 'Alterar un escrito o noticia,  cambiando su sentido: han viciado las respuestas que dio en la entrevista.'],
  vicio: ['m. Excesiva aficin a algo,  especialmente si es perjudicial: el vicio de beber.', 'Mala costumbre,  hbito de obrar mal: tiene el vicio de mentir.'],
