  virgo: ['m. Himen.', 'astron. Uno de los signos del Zodiaco,  al que pertenecen las personas que han nacido entre el 23 de agosto y el 23 de septiembre.  Se escribe con mayscula.'],
  viril: ['m. Vidrio muy claro y transparente que se coloca delante de algunos objetos para preservarlos sin ocultarlos a la vista.', 'Caja de cristal bordeada con oro que encierra la forma consagrada y se coloca en la custodia para la exposicin del Santsimo,  o que guarda reliquias y se coloca en un relicario.'],
  virus: ['m. microbiol. Microorganismo de estructura simple,  constituido por cido nucleico (ADN o ARN) y protena,  que necesita multiplicarse dentro de las clulas vivas y es causa de numerosas enfermedades: la gripe y el sida son enfermedades causadas por virus.', 'inform. Programa que se incorpora a un ordenador a travs de disquetes u otros sistemas de comunicacin,  y que se ejecuta automticamente en determinados momentos modificando o destruyendo los datos contenidos en el ordenador: siempre compruebo que los archivos que introduzco en mi ordenador no contengan virus.  No vara en pl.'],
  visar: ['tr. Reconocer,  examinar la autoridad competente un instrumento,  una certificacin,  un pasaporte,  etc.,  ponindoles el visto bueno,  por lo general para un uso determinado: en la embajada le visaron toda la documentacin.', 'Dirigir la puntera o la visual de un arma de fuego.'],
