  visn: ['m. Nombre comn de diversas especies de mamferos carnvoros de unos 55 cm de longitud,  cuerpo estilizado,  patas con membrana interdigital y un pelaje suave muy apreciado: los visones tienen una cola muy larga.', 'Piel de este animal.'],
