  viudo: ['adj. Persona a quien se le ha muerto su cnyuge y no ha vuelto a casarse: se qued viudo a los pocos aos de casarse. Tambin s.', 'col. [Alimento como las patatas,  los garbanzos o las judas] que se cocinan solos o sin acompaamiento de carne.'],
