  yanta: ['m. Tributo que pagaban,  generalmente en especie,  los habitantes de los pueblos y de los distritos rurales para el mantenimiento del soberano y del seor cuando transitaban por ellos. Ms en pl.', 'Renta que antiguamente se pagaba en especie,  y hoy en dinero,  al poseedor del dominio directo de una finca: el yantar consista en medio pan y una escudilla de legumbres.'],
